.post__item{
    padding: 10px 10px 20px 10px;
    border-radius: 10px;
    background-color: #282D3C;
    margin-bottom: 20px;
    max-height: 600px;
    .link__img{
        margin-bottom: 10px;
        display: block;
        width: 100%;
        height: 300px;
        img{
            width: 100%;
            height: 100%;
            // object-fit: cover;
            object-fit: contain;
        }
    }

    .top__block{
        display: flex;
        .avatar{
            border-radius: 50%;
            width: 40px;
            height: 40px;
        }
        .user__block{
            margin-bottom: 30px;
            margin-left: 10px;
            span{
                display: block;
            }
        }
    }
    .content__block{
        padding: 0px 40px;
        .link{
            margin-bottom: 20px;
            display: inline-block;
            h2{
                font-size: 30px;
                transition: all ease .4s;
                &:hover{
                    color: red;
                }
            }
        }

    }

    .tags__block{
        margin-bottom: 20px;
        .tags{
            padding: 10px 15px;
            margin: 5px 0;
            border-radius: 5px;
            cursor: pointer;
            transition: all ease .4s;
            &:hover{
                background-color: black;
            }
        }
    }
    .bottom__block{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .details__block{
            display: flex;
            .item{
                margin-right: 20px;
                cursor: pointer;
                display: flex;
                align-items: center;
                text-transform: capitalize;
                .icon{
                    margin-right: 10px;
                }
            }
        }
    }
}