.login{
    // margin:calc(50% - 270px) auto;
    position: absolute;
    top: calc(50% - 170px);
    left: calc(50% - 170px);
    padding: 20px;
    border: 2px solid;
    border-radius: 20px;
    width: 340px;
    h2{
        margin-bottom: 20px;
        text-align: center;

    }
    label{
        margin-bottom: 5px;
    }
    input{
        margin-bottom: 20px;
        padding: 7.5px 10px;
        color: black;
        font-weight: 600;
        border-radius: 10px;
        border: none;
    }
    .input__block{
        display: flex;
        flex-direction: column;
    }
    .btn__block{
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

